@import "./scss/mixin.scss";

body {
    width:100%;
    margin:0px;
    padding:0px;
    background:linear-gradient(to bottom, #000, #333);
}
* {
    font-family: 'Poppins', sans-serif;
    -webkit-appearance: none;
}
a {
    text-decoration: none;
}
#webapp {
    width:100%;
    height:100%;
    background:$roxo;
    margin:0px auto;
    overflow-x:hidden;
    .switch-wrapper {
        position: relative;
    }
    .switch-wrapper > div {
        position: absolute;
        background:#FFF;
        width:100%;
        height: 100%;
    }
    > div {
        height:100%;
        width:100%;
        float:left;
    }
    .container {
        width:360px;
        margin:0px auto;
    }
}