@import "../../_assets/scss/mixin.scss";

#purple-layout {
    box-sizing: border-box;
    padding-top:45px;
    background:$roxo;
    height: 100%;
    .container {
        position: relative;
        height:100%;
    }
    form.vertical {
        position: absolute;
        top:50%;
        transform: translate(-0%, -50%);
    }
    button.radius-gradient {
        display: inline-block;
        font-size:16.24px;
        background:linear-gradient(to right, $laranja, $rosa);
        padding:10px 80px;
        color:#FFF;
        font-weight: 600;
        border-radius:100px;
        margin-top:4vh;
        border:none;
        &.full {
            width:100%;
        }
    }
    .group-button {
        width:100%;
        float:left;
        text-align: center;
    }
    .arrow-right {
        background:url(../../_assets/img/icos/arrow-righ.png);
        width:13px;
        height:21px;
        float:left;
    }
    h1 {
        font-size: 21px;
        color:#FFF;
        text-align: center;
        padding-top:0px;
        display: inline-block;
        margin:-6px 0 0 0;
        width:calc(100% - 26px);
        text-align: center;
    }
}