@import './mixin.scss';

#HomePageIndex {
    .container {
        position: relative;
        height:100%;
        width:330px !important;
    }
    background:url(../img/bg-home.jpg) center no-repeat $roxo;
    .logo {
        width:98px;
        height:42px;
        background:url(../img/logo-white.png);
        float:left;
        margin-top:6vh;
    }
    h1 {
        width:100%;
        float:left;
        margin-top:5vh;
        font-size:35px;
        line-height: 45px;
        width:300px;
        color:#FFF;
        b {
            color:$azul;
            font-weight: 700;
        }
    }
    .start {
        float:left;
        font-size:16px;
        background:linear-gradient(to right, $laranja, $rosa);
        padding:15px 40px;
        text-transform: uppercase;
        color:#FFF;
        border-radius:100px;
        margin-top:4vh;
    }
    .bottom-control {
        position: absolute;
        bottom:5vh;
        width:100%;
        left:0px;
        .button-border {
            padding:15px 35px;
            text-transform: uppercase;
            color:#FFF;
            border-radius:100px;
            border:1px solid #FFF;
            float:left;
            font-size:16px;
            box-sizing: border-box;
            cursor:pointer;
            &:nth-child(even) {
                float:right;
            }
            &:hover {
                background:#FFF;
                color:#370a65;
            }
        }
    }
}