.input-white {
    width:100%;
    margin-bottom: 5px;
    float:left;
    position: relative;
    label {
        width:100%;
        font-size:14px;
        color:#FFF;
        box-sizing: border-box;
        padding-left:1px;
    }
    input {
        width:100%;
        font-size:20px;
        color:#FFF;
        height: 44px;
        border:none;
        background:none;
        outline: none;
        border-bottom: 1px solid rgba(255,255,255,0.3);
        box-sizing: border-box;
        padding:0px 0px 13px 0px;
        border-radius:0px;
    }
    .eye-green {
        background:url(../../_assets/img/icos/eye-green.png) center no-repeat;
        width:49px;
        height:100%;
        position: absolute;
        right:0px;
        top:0px;
        margin-top:0px;
        cursor: pointer;
    }
}