@import "../../_assets/scss/mixin.scss";

.checkbox-switch {
    width:100%;
    float:left;
    .switch {
        width:70px;
        height:37px;
        box-sizing: border-box;
        border:2px solid #FFF;
        border-radius: 100px;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        transition: all 0.2s;
        &.active {
            background:$azul;
            border:2px solid $azul;
            .radius {
                left:calc(100% - 35px);
            }
        }
        .radius {
            box-shadow:0px 0px 10px rgba(0,0,0,0.4);
            width:33px;
            height:33px;
            border-radius: 100px;
            background:#FFF;
            position: absolute;
            left:0;
            top:0;
            transition: all 0.2s;
        }
    }
    span {
        font-size:15.6px;
        color:#FFF;
        vertical-align: middle;
        padding-left:16px;
        font-weight: 600;
    }
}